.search-bar {
  background-color: white;
}

.search-bar__locations {
  display: -ms-flexbox;
  display: flex;
}

.locations__inputs {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.locations__inputs--switched {
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
}

.locations__switch-locations {
  cursor: pointer;
  border: none;
  border-bottom: 1px solid #eee;
  background-color: transparent;
  margin: 0;
  padding: .4rem;
}

.locations__switch-locations .fa-exchange {
  font-size: 1.2rem;
  color: #666;
}

.search-bar__datetime {
  display: -ms-flexbox;
  display: flex;
}

.search-bar__search {
  background-color: #EE1844;
  border: none;
  color: white;
  cursor: pointer;
  padding: .8rem;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin: 0;
  width: 100%;
}

.search-bar__search:disabled {
  background-color: #666;
  cursor: default;
}

.search-bar__searching {
  height: 1rem;
  vertical-align: middle;
  margin-left: .4rem;
}



